body, header, footer {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

.about-body {
    background-color: #FFFFFF;;
}

.about-intro {
    min-height: 970px;
    background: linear-gradient(to bottom, #272A33 40%, #FF7324);
}

.about-intro h1 {
    padding-top: 220px;
    margin-left: 60px;
    font-family: 'Chakra Petch', sans-serif;
    font-size: 36px;
    line-height: 1;
    color: #FFFFFF;
}

.about-intro p {
    margin-left: 60px;
    font-family: Inter;
    font-size: 17px;
    position: relative;
    top: -10px;
    line-height: 1.4;
    color: #B3B3B3;
    padding-bottom: 10px;
}

.about-intro .start {
    margin-left: 60px;
    padding: 10px 33px;
    background-color: #FF7324;
}

.about-intro .start:hover {
    background-color: #F05700;
    cursor: pointer;
}

.about-intro .find {
    background-color: #454D60;
}

.about-intro .find:hover {
    background-color: #303542;
    cursor: pointer;
}

.find, .start {
    margin-right: 10px;
    border-radius: 50px;
    border: none;
    padding: 10px 35px;
    font-family: 'Chakra Petch', sans-serif;
    font-weight: 700;
    font-size: 18px;
}

.about-intro a {
    text-decoration: none;
    color: #FFFFFF;
}

.floating-section {
    position: absolute;
    top: 105%;
    display: flex;
    justify-content: space-between;
    background-color: #FFFFFF;
    margin-left: 45px;
    min-height: 530px;
    width: 94.5%;
    border-radius: 10px;
    box-shadow: 1px 1px 40px;
}

.plan {
    margin-left: 70px;
}

.stats {
    margin-right: 70px;
}

.plan img {
    width: 340px;
    height: 340px;
}

.members img {
    width: 300px;
    height: 340px;
}

.stats img {
    width: 300px;
    height: 340px;
}

.plan img, .members img, .stats img {
    padding-left: 30px;
    margin-top: 55px;
}

.plan p, .members p, .stats p {
    text-align: center;
    font-family: Inter;
    font-weight: 700;
    font-size: 20px;
    color: #222732;
    line-height: 1.5;
}

.benefits {
    min-height: 490px;
}

.benefits h1 {
    font-family: 'Chakra Petch', sans-serif;
    font-weight: 400;
    font-size: 35px;
    margin-left: 40px;
    color: #222732;
    margin-top: 500px;
}

.about-diagonal-line {
    border: solid 2px #FF7324;
    width: 5%;
    margin-top: 50px;
    margin-left: 43px;
}

.column {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
    margin-left: 20px;
    width: 83%;
}

.column img {
    width: 70px;
    height: 70px;
}

.column .members {
    position: relative;
    bottom: 50px;
}

.admins img, .brackets img, .support img {
    margin-left: 20px;
}

.column li {
    font-family: Inter;
    font-size: 14px;
    line-height: 1.8;
    color: #333A4A;
}

.features {
    min-height: 800px;
}

.features h1 {
    color: #222732;
    font-family: 'Chakra Petch', sans-serif;
    font-weight: 400;
    font-size: 35px;
    text-align: center;
    margin-top: 70px;
    line-height: 1.2;
}

.row1 img, .row2 img {
    width: 360px;
    height: 325px;
}

.row1, .row2 {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    margin-left: 45px;
    margin-right: 45px;
}

.row2 {
    position: relative;
    bottom: 140px;
}

.gamers, .athletes, .casuals, .enthusiasts, .patrons, .coworkers, .fans, .intramural {
    position: relative;
}
  
