.background {
    position: relative;
    bottom: 114px;
    min-height: 50px;
    background: linear-gradient(to right bottom, transparent 50%, #FF8038D9 50%);
}

.background1 {
    position: relative;
    bottom: 114px;
    min-height: 60px;
    background: #FF8038D9;
}

.floating-heading {
    position: relative;
    bottom: 250px;
}

.skewed-section {
    min-height: 100px;
    background: linear-gradient(to right bottom, transparent 50%, #252830 50%);
}

.start-community {
    background: #252830;
    display: flex;
    justify-content: space-between;
    min-height: 300px;
}

.start-community h1 {
    color: #FFFFFF;
    font-size: 35px;
    line-height: 0.2;
    margin-left: 220px;
    margin-top: 145px;
}

.start-community p {
    color: #CCCCCC;
    font-size: 14px;
    font-family: Inter;
    margin-left: 220px;
}

.about-span {
    color: #2BBFFF;
}

.about-span:hover {
    cursor: pointer;
}

.try-button {
    padding: 10px 33px;
    background-color: #FF7324;
    border-radius: 50px;
    border: none;
    padding: 10px 35px;
    font-family: 'Chakra Petch', sans-serif;
    font-weight: 700;
    font-size: 18px;
    position: relative;
    top: 150px;
    right: 220px;
    color: #FFFFFF;
}

.try-button:hover {
    background-color: #F05700;
    cursor: pointer;
}